import styled, { css } from 'styled-components';
import theme from '../theme';

export const StyledButtonComponent = styled.button`
  height: 38px;
  width: max-content;
  padding: 11px 28px;
  cursor: pointer;
  border-radius: 4px;
  font-size: 14px;
  margin-right: 10px;
  margin-top: 10px;
  font-family: Arial;
  font-weight: 600;
  text-transform: uppercase;

  ${({ type, colorEffect, hovers }) => {
    const isOutlined = type === 'outlined';
    const isTextButton = type === 'textButton';
    const bgColor = colorEffect ? 'transparent' : theme?.[type];
    const textColor = colorEffect ? theme?.[type] : 'white';
    const borderColor = isOutlined
      ? textColor
      : isTextButton
      ? 'transparent'
      : 'white';

    return css`
      background-color: ${bgColor};
      color: ${textColor};
      ${
        isOutlined &&
        'border: 2px solid ' +
          textColor +
          ';' /* Add border only for outlined type */
      }

      ${colorEffect &&
      !isOutlined &&
      hovers &&
      css`
        &:hover,
        &:active {
          background-color: ${theme?.[type]};
          color: white;
        }
      `}
    `;
  }}
`;
